
.main__professional-project-div{
    position: relative;
    width: 300px;
    height: 200px;
    background-color: white;
    transition: .25s;
    overflow: hidden;
    box-shadow: 0 0 2px 2px black;
    text-align: center;
}

.main__professional-project-div:active{
    transform: scale(1);
    transition: .25s;
    cursor: pointer;
}

.main__professional-project-anchor{
    width: 100%;
    height: 100%;
}

.main__professional-project-image{
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.main__professional-project-info{
    top: 0;
    left:0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(8,8,8,.7);

    display: flex;
    flex-direction: column;
    justify-content: center;
    pointer-events: none;
}

.main__professional-project-title{
    text-align: center;
    color: white;
    word-wrap: break-word;
    -webkit-text-stroke: .5px lightgreen;
}


.main__professional-project-techs{
    text-align: center;
    color: white;
    word-wrap: break-word;
    -webkit-text-stroke: .5px lightskyblue;
}

.main__professional--hidden{
    display: none;
}